

import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'



export default {
components: {
TransferDom, Actionsheet, Group, XSwitch, Cell,XHeader,CellBox ,Datetime,XInput,XTextarea,XButton,
Toast
},
name: 'qianKa',
mounted(){
this.$root.v.$emit('form',123)
},
methods:{
detailBtn(){
this.$router.push({path:"/apply/qianKa/qiankaDetail"})
},
select(n){
this.show=true;
this.selectType=n
if(n==1){
this.menu={
1: '上班卡',
2: '下班卡',
3: '上下班卡',
}
}else if (n==2){
this.menu={
1: '公事',
2: '私事'
}
}
},
checkform(){

if(this.beginDate == ""){
this.errMsgToast=true
this.errMsgText="请选择时间"
return false
}else if(this.form.type1==""){
this.errMsgToast=true
this.errMsgText="刷卡类型"
return false
}else if(this.form.type2==""){
this.errMsgToast=true
this.errMsgText="签卡类型"
return false
}else if(this.form.reason==""){
this.errMsgToast=true
this.errMsgText="请输入原因"
return false
}
return true
},
formSubmit(){
if(!this.checkform())
return
this.$http.post("/MobileService/MyApply.asmx/AddQKRecord",this.form)
.then(r=>{
console.log(r);
let data= JSON.parse(r.data.d)
console.log(data);
this.successMsgToast=true
this.successMsgText=data.msg
})
.catch(e=>{
console.log(e);
})
console.log(this.form);
},
onClick (key,value) {
if(this.selectType==1){
this.selectValue1=value;
this.form.type1=key
}else if(this.selectType==2){
this.selectValue2=value;
this.form.type2=key
}
console.log(key)
},
change(){
console.log(this.beginDate);
let d = this.beginDate.split(" ")
this.form.qkDate=d[0]
this.form.qkTime=d[1]
},
upload(e){
this.uploadName=[]
console.log(e.target.files);
for(var f in e.target.files){



if(typeof e.target.files[f] == "object"){
this.uploadName.push(e.target.files[f].name)
}

}
},
},

data(){
return{
form:{
empName:window.localStorage.getItem("global_empname"),
empId:window.localStorage.getItem("global_empid"),
company:window.localStorage.getItem("comp_code"),
fieldCustomParams:"[]",
reason:"",
type1:"",
type2:"",
},
errMsgToast:false,
successMsgToast:false,
errMsgText:"",
successMsgText:"",
selectType:"",
stringValue: '0',
show: false,
selectValue1:"请选择",
selectValue2:"请选择",
beginDate:"",
uploadName:[],
menu:null
}
}

}




