

import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

import qs from 'qs'
import {getScrollTop,getWindowHeight,getScrollHeight} from "@/common/util"

export default {
name: '',
mounted(){
this.getListData()




},
computed:{
isLoading(){
},
fHeight(){
let fHeight= (parseFloat(document.documentElement.style.fontSize)+42)*0.95
fHeight = (-fHeight).toString()
return fHeight
},
},
methods:{
toAnnouncementDC(id){
this.$router.push({path:"/apply/AnnouncementDetails/AnnouncementDC"})
},
scroll(){
var g = getScrollTop() + getWindowHeight()
if (g  === getScrollHeight()) {
console.log("到底部");
if(this.loadDataSwitch){
this.getListData(this.pageIndex+=1)
}
}
},

btn(){
this.$router.go(-1)
},

getListData(){
const empName=window.localStorage.getItem("global_empname")
const empId=window.localStorage.getItem("global_empid")
const company=window.localStorage.getItem("comp_code")

let formData={

empId: empId ,


id:this.$route.query.type
}

this.$http.post("/MobileService/Web/Handler/hdlGetNotice.ashx",qs.stringify(formData),{ContentType:"application/x-www-form-urlencoded"})
.then(r=>{
let data=r.data
console.log(data);
if(data){
this.content=data.content
this.senddate=data.senddate
this.subject=data.subject


}else{
this.loadDataSwitch=false
this.loadMoreDom=false
this.loadMoreFinish=true
}


}).catch(err=>{
this.loadMoreDom=false
})
},
detailBtn(){

},
formSubmit(){

}
},
data () {
return {
subject:"",
senddate:"",
content:"",
loadDataSwitch:true,
pageIndex:1,
loadMoreDom:false,
loadMoreFinish:false,
fontHeight:0,
scrollList:20,
listData:[],
msg: 'Welcome to Your Vue.js App',
}
},
components: {
Actionsheet,
XSwitch,
Group,
Cell,
XHeader,
CellBox,
Datetime,
XInput,
XTextarea,
XButton,Toast,GroupTitle,Scroller,LoadMore,Loading,Badge
},
destroyed(){
window.removeEventListener("scroll",this.scroll)
},
}




