export default {
name: 'App',
components:{
Loading,Spinner,Toast
},
data(){
return{
}
},
beforeCreate(){
},
mounted(){
},
updated(){

},
computed:{
isLoading(){
return this.$store.state.loading
},
errMsgText(){
return this.$store.state.errMsgText
},
