

import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'


import qs from 'qs'
export default {
components: {
Actionsheet,
XSwitch,
Group,
Cell,
XHeader,
CellBox,
Datetime,
XInput,
XTextarea,
XButton,Toast,Confirm,Checklist
},
methods:{
changeChecklist(a,b){
console.log(a);
this.ActorsId=a
},
onConfirm(){
console.log("确定");
let formData={
receiptID:this.receiptID,
workFlowCode:"K001",
Actors: JSON.stringify(this.ActorsId),
global_empid:this.form.empId,
comp_code:this.form.company,
nodeID:1,
}
this.$http.get("/MobileWeb/MyApply/IsAllowSelectorHandler.ashx",formData)
.then(r=>{
console.log(r);
if(r.IsSuccess==true){
this.successMsgToast=true
this.Message=r.Message
}else{
this.successMsgToast=true
this.Message="出错啦"
}
})
.catch(e=>{
console.log(e);
})

},
onCancel(){
console.log("取消");
},
onShow(){
console.log("show");
},
onHide(){
console.log("hide");
},
detailBtn(){
this.$router.push({path:"/apply/vacation/details"})
},
checkForm(form){
console.log(form);
if(form.type==""){
this.errMsg="请输入请假类型"
this.errMsgToast=true
return false;
}else if(form.startDate==""){
this.errMsg="请输入开始时间"
this.errMsgToast=true
return false;
}else if(form.endDate==""){
this.errMsg="请输入结束时间"
this.errMsgToast=true
return false;
}else if(form.AlDay==""){
this.errMsg="请输入天数"
this.errMsgToast=true
return false;
}else if(form.AlTime==""){
this.errMsg="请输入小时"
this.errMsgToast=true
return false;
}else if(form.reason==""){
this.errMsg="请输入请假原因"
this.errMsgToast=true
return false;
}
return true
},
formSubmit(){
this.form.startDate=this.form.startDate.split(" ")[0];
this.form.endDate=this.form.endDate.split(" ")[0];
console.log(this.form)
if(!this.checkForm(this.form)) return
this.$http.post("/MobileService/MyApply.asmx/AddAskLeaveRecord",this.form)
.then(r=>{
console.log(r);
let data = JSON.parse(r.data.d)
if(data.success==1){
this.checkbox=true
this.actors=data.actors
this.receiptID=data.id
console.log(this.actors);
for(let i=0;i<this.actors.length;i++){
console.log(this.actors[i]);
this.commonList.push({key:JSON.stringify(this.actors[i].EmpID),value:this.actors[i].EmpName+" ["+this.actors[i].EmpCode+"]"})
}
}else if(data.success=="0"){
this.errMsg=data.msg
this.errMsgToast=true
}})

},
select(){
this.show=true;
},
onClick (key,value) {
console.log(value)
this.form.type=value;
},
change(){
},
upload(e){
this.form.uploadName=[]
console.log(e.target.files);
for(var f in e.target.files){
if(typeof e.target.files[f] == "object"){
this.form.uploadName.push(e.target.files[f].name)
}

}
}
},
mounted(){
this.form.empName=window.localStorage.getItem("global_empname")
this.form.empId=  window.localStorage.getItem("global_empid")
this.form.company=window.localStorage.getItem("comp_code")
},
computed:{

},
data(){
return{
Message:"",
successMsgToast:false,
receiptID:"",
checklistModel:[],
commonList: [],
checkbox:false,
actors:[],
ActorsId:[],
errMsgToast:false,
errMsg:"",
form:{
type:"",
startDate:"",
endDate:"",
Attach:"",
company:"",
empId:"",
empName:"",
wholeDay:"1",
startTime:"",
endTime:"",
reason:"",
syDays:"",
AlDay:"",
AlTime:"",
ALDeputy:"0",
fieldCustomParams:"[]",
},
stringValue: '0',
show: false,
selectValue:"请选择",
value:['2017-01-15', '03', '05'],
uploadName:[],
menu: {
menu1: '病假',
menu2: '出差',
menu3: '产假',
menu4: '工伤',
menu5: '婚假',
menu6: '年假',
menu7: '丧假',
menu8: '事假',
menu9: '调休',
menu11: '停工待料',
},
}
}
}




