


import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Confirm from 'vux/src/components/confirm/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {global_empname,global_empid,comp_code} from "@/common/util"


export default {
components: {
Actionsheet,
XSwitch,
Group,
Cell,
XHeader,
CellBox,
Datetime,
XInput,
XTextarea,
XButton,Toast,Confirm,Checklist
},
methods:{
detailBtn(){
this.$router.push({path:"/apply/jiaBan/jiaBanDetails"})
},
changeChecklist(a,b){
console.log(a);
this.ActorsId=a
},
checkForm(){
if(this.form.beginDate==""){
this.errMsgToast=true
this.Message="请输入开始时间"
return false
}else if(this.form.endDate==""){
this.errMsgToast=true
this.Message="请输入结束时间"
return false
}else if(this.form.otKouTime==""){
this.errMsgToast=true
this.Message="扣休息时间"
return false
}else if(this.form.reason==""){
this.errMsgToast=true
this.Message="请输入原因"
return false
}

const bDate=this.form.beginDate
const eDate=this.form.endDate
console.log( bDate);
console.log(eDate);
if(bDate>=eDate){
this.errMsgToast=true
this.Message="结束时间必须大于开始时间"
return false
}
return true
},
select(){
this.show=true;
},
onClick (key,value) {
console.log(value)
this.selectValue=value;
},
onConfirm(){
console.log("确定");
let formData={
receiptID:this.receiptID,
workFlowCode:"K002",
Actors: JSON.stringify(this.ActorsId),
global_empid:global_empid,
comp_code:comp_code,
nodeID:1,
}
this.$http.get("/MobileWeb/MyApply/IsAllowSelectorHandler.ashx",formData)
.then(r=>{
console.log(r);
if(r.IsSuccess==true){
this.successMsgToast=true
this.Message=r.Message
this.commonList=[]
}else{
this.errMsgToast=true
this.Message=r.Message
}
})
.catch(e=>{
console.log(e);
})

},
formSubmit(){

if(!this.checkForm())return

const formData ={
company:comp_code,
empId:global_empid,
empName: global_empname,
otDate:this.form.beginDate.split(" ")[0],
otSTime:this.form.beginDate.split(" ")[1],
otETime: this.form.endDate.split(" ")[1],
reason:this.form.reason,
drpSTimeFlag:'',
drpETimeFlag:'',
otKouTime:this.form.otKouTime,
fieldCustomParams:'[]'
}
console.log(formData);

this.$http.post("/MobileService/MyApply.asmx/AddOutTimeRecord",formData)
.then(r=>{
console.log(r);
let data= JSON.parse(r.data.d)
console.log(data);
if(data.success==1){
this.checkbox=true
this.actors=data.actors
this.receiptID=data.id
console.log(this.actors);
for(let i=0;i<this.actors.length;i++){
console.log(this.actors[i]);
this.commonList.push({key:JSON.stringify(this.actors[i].EmpID),value:this.actors[i].EmpName+" ["+this.actors[i].EmpCode+"]"})
}
}else if(data.success=="0"){
this.Message=data.msg
this.errMsgToast=true
}
})
.catch(e=>{
})
},
change(){
console.log(this.beginDate);
},
upload(e){
this.uploadName=[]
console.log(e.target.files);
for(var f in e.target.files){



if(typeof e.target.files[f] == "object"){
this.uploadName.push(e.target.files[f].name)
}

}
}
},
data(){
return{
form:{
otKouTime:"",
reason:"",
beginDate:"",
endDate:"",
},
commonList: [],
receiptID:"",
checkbox:false,
actors:[],
ActorsId:[],
Message:"",
errMsgToast:false,
successMsgToast:false,
stringValue: '0',
show: false,
selectValue:"请选择",
uploadName:[],
menu: {
menu1: '方式1',
menu2: '方式2',
menu3: '方式3',
menu4: '方式4',
},
}
}
}




